
.connection,
.headingWrapper {
	width: 100%;
}
.valueFilterSelect {
	width: 83px;
	height: 38px;
}
.connection > div {
	margin-bottom: 10px;
}
.date-picker {
	width: 128px;
}
.vue-star-rating {
	text-align: center;
	display: block !important;
}
.actions {
	text-align: center;
}
@media (max-width: 767px) {
.vue-star-rating > .vue-star-rating,
	.dueDate,
	.actions {
		text-align: left;
}
}
.profileImg {
	width: 80px;
	border-radius: 80px;
}
.infoText {
	padding-left: 32px;
	min-height: 80px;
	max-height: 120px;
	overflow-y: auto;
}
textarea {
	resize: none;
	width: 100%;
	height: 120px;
	background-color: transparent;
	border: 1px solid rgb(204, 204, 204);
	border-radius: 4px !important;
}
::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	border-radius: 10px;
	background-color: #f5f5f5;
}
::-webkit-scrollbar {
	width: 10px;
	background-color: #f5f5f5;
}
::-webkit-scrollbar-thumb {
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	background-color: #555;
}
.input-wrapper {
	border-radius: 4px !important;
}
.input-wrapper > .input {
	line-height: 18px !important;
}
.vue-input-tag-wrapper {
	border-radius: 4px !important;
}
.actions > button {
	display: inline-block;
}
.doneBtn {
	margin-right: 4px;
	color: #ffffff;
	background-color: #24c602;
}
.doneBtn:hover {
	background-color: #1e8f08;
}

.row {
	margin-bottom: 15px;
}
.searchHeading > .headingWrapper {
	margin-bottom: 10px;
}
.headingWrapper {
	margin-bottom: 10px;
}
.dateHead {
	text-align: left !important;
}
.activeSort,
.activeSort:active,
.activeSort:visited {
	color: blueviolet;
}
select {
	border-radius: 4px;
	cursor: pointer;
}

.row {
	margin-bottom: 15px;
	padding-bottom: 15px;
	border-bottom: 1px solid black;
}
.activeSort,
.activeSort:active,
.activeSort:visited {
	color: blueviolet;
}
